import React, { useState } from "react";
import {
  Grid,
  Box,
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  TextField,
  Badge,
  borderRadius,
  styled,
  InputBase,
  Stack,
  Autocomplete,
  Avatar,
  Button,
  Menu,
  MenuItem,
  ButtonGroup,
  Checkbox,
  Skeleton,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
} from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import imagenave from "../images/٢٠٢١١١٢٥_١١٥١٥٩.jpg";
import DensityMediumIcon from "@mui/icons-material/DensityMedium";
import MailIcon from "@mui/icons-material/Mail";
import WbSunnyIcon from "@mui/icons-material/WbSunny";
import ShareIcon from "@mui/icons-material/Share";
import NightlightIcon from "@mui/icons-material/Nightlight";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import AssignmentIndIcon from "@mui/icons-material/AssignmentInd";
import LogoutIcon from "@mui/icons-material/Logout";

import HomeIcon from "@mui/icons-material/Home";
import ArticleIcon from "@mui/icons-material/Article";
import PeopleIcon from "@mui/icons-material/People";
import StorefrontIcon from "@mui/icons-material/Storefront";
import PersonAddIcon from "@mui/icons-material/PersonAdd";
import SettingsIcon from "@mui/icons-material/Settings";
import AccountBoxIcon from "@mui/icons-material/AccountBox";

const StyleToolbar = styled(Toolbar)({
  display: "flex",
  justifyContent: "space-between",
});
const Search = styled("div")(({ theme }) => ({
  backgroundColor: "white",
  padding: "0 10px",
  borderRadius: theme.shape.borderRadius,
  width: "40%",
}));
const Icoonnave = styled(Box)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  gap: "20px",
}));

const Navebar = ({ mode, setmode }) => {
  const [open, setopen] = useState(false);
  const [openNve, setopenNve] = useState(false);
  return (
    <>
      <Box position="static">
        <AppBar>
          <Drawer open={openNve} onClose={() => setopenNve(false)}>
            <Box>
              <List>
                <ListItem>
                  <ListItemButton>
                    <HomeIcon />
                  </ListItemButton>
                </ListItem>
                <ListItem>
                  <ListItemButton>
                    <ArticleIcon />
                  </ListItemButton>
                </ListItem>
                <ListItem>
                  <ListItemButton>
                    <PeopleIcon />
                  </ListItemButton>
                </ListItem>
                <ListItem>
                  <ListItemButton>
                    <StorefrontIcon />
                  </ListItemButton>
                </ListItem>
                <ListItem>
                  <ListItemButton>
                    <PersonAddIcon />
                  </ListItemButton>
                </ListItem>
                <ListItem>
                  <ListItemButton>
                    <SettingsIcon />
                  </ListItemButton>
                </ListItem>
                <ListItem>
                  <ListItemButton>
                    <AccountBoxIcon />
                  </ListItemButton>
                </ListItem>
              </List>
            </Box>
          </Drawer>
          <StyleToolbar>
            <Typography
              sx={{
                display: { xs: "none", md: "block" },
              }}
              variant="h4"
            >
              mostfa
            </Typography>
            <IconButton>
              <DensityMediumIcon
                onClick={() => setopenNve(true)}
                sx={{
                  display: { xs: "flex", md: "none" },
                  color: "white",
                }}
              />
            </IconButton>
            <Search>
              <SearchIcon color="primary" sx={{ display: { xs: "none" } }} />
              <InputBase placeholder="searh ..." />
            </Search>
            <Icoonnave>
              <Badge
                badgeContent={2}
                color={"error"}
                sx={{
                  display: { xs: "none", md: "block" },
                }}
              >
                <AssignmentIndIcon />
              </Badge>
              <Badge
                badgeContent={1}
                color={"error"}
                sx={{
                  display: { xs: "none", md: "block" },
                }}
              >
                <MailIcon />
              </Badge>
              <Checkbox
                onChange={() => setmode(mode === "dark" ? "light" : "dark")}
                icon={<NightlightIcon />}
                checkedIcon={<WbSunnyIcon sx={{ color: "white" }} />}
              />
              <IconButton>
                <Avatar
                  alt="Remy Sharp"
                  src={imagenave}
                  sx={{ width: 30, height: 30 }}
                  onClick={(e) => setopen(true)}
                />
              </IconButton>
            </Icoonnave>
            <Menu
              id="demo-positioned-menu"
              aria-labelledby="demo-positioned-button"
              open={open}
              onClose={() => setopen(false)}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
            >
              <MenuItem onClick={(e) => setopen(false)}>Profile</MenuItem>
              <MenuItem onClick={(e) => setopen(false)}>My account</MenuItem>

              <MenuItem onClick={(e) => setopen(false)}>
                <LogoutIcon sx={{ mr: 1 }} /> Logout
              </MenuItem>
            </Menu>
          </StyleToolbar>
        </AppBar>
      </Box>
    </>
  );
};

export default Navebar;
