import {
  Alert,
  Avatar,
  AvatarGroup,
  Box,
  Button,
  ButtonGroup,
  Fab,
  IconButton,
  Modal,
  styled,
  TextField,
  Tooltip,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import AddIcon from "@mui/icons-material/Add";
import image1 from "../images/٢٠٢١١١٢٥_١١٥١٥٩.jpg";
import { Stack } from "@mui/system";
import VideoCameraBackIcon from "@mui/icons-material/VideoCameraBack";
import AddAPhotoIcon from "@mui/icons-material/AddAPhoto";
import AddPhotoAlternateIcon from "@mui/icons-material/AddPhotoAlternate";
import ControlPointIcon from "@mui/icons-material/ControlPoint";
const Usersx = styled(Box)({
  display: "flex",
  alignItems: "center",
  gap: 5,
});

const Add = () => {
  const [open, setOpen] = useState(false);

  return (
    <>
      <Tooltip
        onClick={() => setOpen(true)}
        title="Add"
        sx={{
          position: "fixed",
          left: { xs: "43%", sm: 20 },
          bottom: 22,
        }}
      >
        <Box p={2}>
          <Fab color="primary" aria-label="add">
            <AddIcon />
          </Fab>
        </Box>
      </Tooltip>
      <Modal
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box
          bgcolor="background.default"
          color={"text.primary"}
          sx={{
            position: "relative",
            justifyContent: "center",
            left: "50%",
            top: "50%",
            transform: "translate(-50%, -50%)",
            alignItems: "center",
            p: 3,
            width: "500px",
            height: "400px",
            borderRadius: 6,
          }}
        >
          <Typography
            id="modal-modal-title"
            variant="h6"
            component="h2"
            textAlign="center"
            pb={2}
          >
            Create post mostfa
          </Typography>
          <Usersx>
            <Avatar alt="Remy Sharp" src={image1} />
            <Typography variant="h6" ml={1}>
              mostfa safi
            </Typography>
          </Usersx>
          <TextField
            sx={{
              pt: 4,
            }}
            fullWidth
            id="standard-multiline-static"
            multiline
            rows={4}
            variant="standard"
            placeholder="Whats on your mind?"
          />
          <Stack direction="row" spacing={3} py={3}>
            <VideoCameraBackIcon color="primary" />
            <AddAPhotoIcon color="secondary" />
            <AddPhotoAlternateIcon color="success" />
            <ControlPointIcon color="warning" />
          </Stack>
          <ButtonGroup fullWidth variant="contained">
            <Button sx={{ width: "80%" }}>
              <Typography variant="h6" fontWeight={500}>
                Add post
              </Typography>
            </Button>
            <Button sx={{ width: "20%" }}>
              <AddPhotoAlternateIcon />
            </Button>
          </ButtonGroup>
        </Box>
      </Modal>
    </>
  );
};

export default Add;
