import {
  Avatar,
  AvatarGroup,
  Box,
  Button,
  Divider,
  ImageList,
  ImageListItem,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Typography,
} from "@mui/material";
import React from "react";
import image1 from "../images/117-500x200.jpg";
import image2 from "../images/122-500x200.jpg";
import image3 from "../images/199-500x200.jpg";
import image4 from "../images/201-500x200.jpg";
import image5 from "../images/239-500x200.jpg";
import image6 from "../images/293-500x200.jpg";
import image7 from "../images/363-500x200.jpg";
import AccessibilityIcon from "@mui/icons-material/Accessibility";
const Righibar = () => {
  return (
    <Box
      flex={2}
      pt={10}
      sx={{
        display: { xs: "none", sm: "block" },
      }}
    >
      <Box position="fixed" p={2}>
        <Typography pb={2} variant="h6" fontWeight={100}>
          Friends mostfa
        </Typography>
        <AvatarGroup max={6}>
          <Avatar alt="Remy Sharp" src={image1} />
          <Avatar alt="Travis Howard" src={image2} />
          <Avatar alt="Cindy Baker" src={image3} />
          <Avatar alt="Agnes Walker" src={image4} />
          <Avatar alt="Agnes Walker" src={image5} />
          <Avatar alt="Agnes Walker" src={image6} />
          <Avatar alt="Agnes Walker" src={image7} />
          <Avatar alt="Agnes Walker" src={image7} />
        </AvatarGroup>
        <Typography pb={2} variant="h6" fontWeight={100}>
          Latest photos
        </Typography>
        <ImageList cols={3} rowHeight={100} gap={4}>
          <ImageListItem>
            <img src={image1} alt="mostfa" loading="lazy" />
          </ImageListItem>
          <ImageListItem>
            <img src={image2} alt="mostfa" loading="lazy" />
          </ImageListItem>
          <ImageListItem>
            <img src={image3} alt="mostfa" loading="lazy" />
          </ImageListItem>
        </ImageList>
        <Typography variant="h6" fontWeight={100}>
          Listes Conversations
        </Typography>
        <List
          sx={{ width: "100%", maxWidth: 560, bgcolor: "background.paper" }}
        >
          <ListItem alignItems="flex-start">
            <ListItemAvatar>
              <Avatar alt="Remy Sharp" src={image5} />
            </ListItemAvatar>
            <ListItemText
              primary="Brunch this weekend?"
              secondary={
                <React.Fragment>
                  <Typography
                    sx={{ display: "inline" }}
                    component="span"
                    variant="body2"
                    color="text.primary"
                  >
                    Ali Connors
                  </Typography>
                  {" — I'll be in your neighborhood doing errands this…"}
                </React.Fragment>
              }
            />
          </ListItem>
          <Divider variant="inset" component="li" />
          <ListItem alignItems="flex-start">
            <ListItemAvatar>
              <Avatar alt="Travis Howard" src={image4} />
            </ListItemAvatar>
            <ListItemText
              primary="Summer BBQ"
              secondary={
                <React.Fragment>
                  <Typography
                    sx={{ display: "inline" }}
                    component="span"
                    variant="body2"
                    color="text.primary"
                  >
                    to Scott, Alex, Jennifer
                  </Typography>
                  {" — Wish I could come, but I'm out of town this…"}
                </React.Fragment>
              }
            />
          </ListItem>
          <Divider variant="inset" component="li" />
          <ListItem alignItems="flex-start">
            <ListItemAvatar>
              <Avatar alt="Cindy Baker" src={image3} />
            </ListItemAvatar>
            <ListItemText
              primary="Oui Oui"
              secondary={
                <React.Fragment>
                  <Typography
                    sx={{ display: "inline" }}
                    component="span"
                    variant="body2"
                    color="text.primary"
                  >
                    Sandra Adams
                  </Typography>
                  {" — Do you have Paris recommendations? Have you ever…"}
                </React.Fragment>
              }
            />
          </ListItem>
        </List>
        <Box
          sx={{
            pt: 1,
            display: { xs: "none", lg: "flex" },
            justifyContent: "center",
            alignContent: "center",
          }}
        >
          <Button variant="outlined">
            <Typography variant="h4">
              mostfa safi
              <AccessibilityIcon fontSize="22px" sx={{ color: "green" }} />
            </Typography>
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default Righibar;
